          <router-link to="/" style="float: left">
            <span class="navbar-site-name">迅仔的博客 </span>
            <span class="secondTitle">始于颜值、陷入才华、忠于人品</span>
          </router-link>
          <div class="navbar-toggler">
            <svg class="icon" style="font-size:1.2em;">
              <title>menu</title>
              <use xlink:href="#icon-menu"></use>
            </svg>
          </div>
          <div class="navbar-links">
            <router-link class="navbar-link" to="/">首页</router-link>
            <router-link class="navbar-link" to="/article">文章</router-link>
            <router-link class="navbar-link" to="/Life">生活</router-link>
          </div>
        </div>
      </nav>

      <!-- 解决顶部导航栏固定后 文档流缺失的部分 -->
