      <!-- 标题 -->
      <!-- <div class="banner">
        <div class="container">
          <div class="center">
            <h1>首页</h1>
          </div>
        </div>
      </div> -->
    </header>

    

    <div class="container">
      <!-- 主体 -->
      <main class="main shadow">
        <router-view class="markdown"></router-view>
      </main>

      <!-- 个人信息栏 -->
      <aside class="aside shadow">
        <section style="background-color: #fff;">
          <!-- 头像 -->
          <div class="touxiang">
            <img src="../assets/images/touxiang.jpg" alt="头像"/>
          </div>
          <!-- 简介 -->
          <div class="info-card-body">
            <section class="info-nickname">迅仔的博客</section>
            <section class="info-desc">
              {{'一生专注一件事 --> 敲代码'}}
              <br>
              {{'一生只爱一个人 --> ***'}}
            </section>
          </div>
          <!-- 个人信息 -->
