    <div v-show="backTopShow">
      <img class="back" @click="backTop" src="../assets/images/back.png" alt="返回"/>
    </div>
  </section>
</template>

<script>
export default {
  name: "HelloWorld",
  data() {
    return { 
      isflex: false, // 控制头部导航栏是否固定
      //是否显示回到顶部
      backTopShow: false,
      // 多少毫秒返回到顶部
      backSeconds: 100
    };
  },
  mounted() {
    window.addEventListener("scroll", () => {
      var scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      if (scrollTop > 0) {
        this.isflex = true;
      } else {
        this.isflex = false;
      }

      if (scrollTop > 400) {
        this.backTopShow = true;
      } else {
        this.backTopShow = false;
      }
    });
  },
  methods: {
    backTop() {
      this.backTopShow = false;
      var dom = document.documentElement || document.body;
      var step = dom.scrollTop / this.backSeconds;
      var backTopInterval = setInterval(function() {
        if (dom.scrollTop > 0) {
          dom.scrollTop -= step;
        } else {
          clearInterval(backTopInterval);
        }
      }, 1);
    }
  }
};
</script>

